nav {
  width: 100vw;
  height: auto;
  display: grid;
  place-items: center;
  /* background: rgba(0,0,0,0.65); */
  background: #1f1f38;
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  /* padding: 2rem; */
  /* border:1px solid blue; */
}

.nav__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  /* border: 1px solid red; */
  /* background: rgba(0,0,0,0.35); */
  /* margin-bottom: 3rem; */
}

.nav__logo {
  width: 2.5rem;
  overflow: hidden;
}

.nav__menu {
  display: flex;
  gap: 3.5rem;
  /* background: rgba(0,0,0,0.35); */
}

.modal {
  width: 100vw;
  height: 100vh;
  /* border: 1px solid red; */
  background: rgba(0, 0, 0, 0.85);
}

.menu-btn {
  visibility: hidden;
  position: relative;
  display: block;
  cursor: pointer;
  justify-content: space-between;
  right: 0;
  /* border: 1px solid white; */
}

.btn-hamburgur {
  position: relative;
  right: 0;
}

.content {
  background: rgba(0, 0, 0, 0.85);
  /* color: black; */
  position: relative;
  top: 0;
  height: 100vh;
  text-align: center;
  padding: 2rem;
  /* border: 1px solid red; */
}

@media screen and (max-width: 1024px) {
  .nav__menu {
    display: none;
    /* border: 1px solid red; */
  }
  .menu-btn {
    visibility: visible;
    display: block;
    align-items: flex-end;
    /* border: 1px solid red; */
    /* width: 100vw; */
  }
  .nav__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90vw;
    /* padding: 2rem 1.5rem 2rem; */
    /* border:1px solid white; */
    margin: auto;
  }
}

